.radial-wrapper::after {
  /*  background: #fff;   */
  content: "";
  background: rgb(0, 0, 0);
  border-radius: 50%;
  width: 156px;
  height: 156px;
  position: absolute;
  top: 12px;
  left: 12px;
}

